textarea {
  background-color: #fff;
  border: 1px solid #ccc;
  height: 100%;
  width: 90%;
  padding: 0.7em;
  border-radius: 0.5em;
  resize: none;
  font-family: fa-brands-400;
  box-shadow: 0.1em 0.1em 0.1em 0 #ccc;
  position: relative;
  overflow-x: hidden;
}

.editArea {
  width: 100%;
  height: 100%;
}
